.home{
    overflow-x:hidden ;
}
.Navbar-name{
    color: #fff;
}
.navbar{
    background-color: rgb(94, 76, 130);
}
.nav-all-link{
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding: 0;
}
.section-content{
    position: relative;
    animation-fill-mode: forwards;
}
.section-content{
    animation: rightThenLeft 3s ease-in-out;
}
@keyframes rightThenLeft {
    from {left:100%;}
  to {left: 0px;}
}
.scroll-tops{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border-radius: 50px;
    background-color: rgb(6, 131, 177);
    position:sticky;
    bottom: 10px;
    left: 87%;
}